import React from "react"
import { graphql } from "gatsby"
import Container from "../components/container"
import SEO from "../components/seo"

export default ({ data }) => (
  <Container>
    <SEO description="About The Site" />
    <h1>My Site's files</h1>
    <table>
      <thead>
        <tr>
          <th>relativePath</th>
          <th>prettySize</th>
          <th>extension</th>
          <th>birthTime</th>
        </tr>
      </thead>
      <tbody>
        {data.allFile.edges.map(({ node }, index) => (
          <tr key={index}>
            <td>{node.relativePath}</td>
            <td>{node.prettySize}</td>
            <td>{node.extension}</td>
            <td>{node.birthTime}</td>
          </tr>
        ))}
      </tbody>
    </table>
  </Container>
)

// Only page components can do this. So the Container component
// if it wants to use data, will have to look somewhere else!!
export const query = graphql`
  query {
    site {
      siteMetadata {
        title
        author
      }
    }
    allFile {
      edges {
        node {
          relativePath
          prettySize
          extension
          birthTime(fromNow: true)
        }
      }
    }
  }
`
